<template>
  <div id="appR">
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col
        :span="12"
        style="height:100px"
      >
      </el-col>
    </el-row>
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col :span="12">
        <el-container>
          <el-main style="display:flex;justify-content:center;">
            <el-card
              class="el-card"
              shadow="always"
            >
              <h1 style="text-align: center;"></h1>
              <el-divider></el-divider>
              <v-form
                ref="form"
                v-model="valid"
                lazy-validation
              >
                <v-text-field
                  v-model="user.username"
                  :rules="passwordRules"
                  type="text"
                  label=""
                  required
                ></v-text-field>
                <v-radio-group
                  label=""
                  mandatory
                  row
                  v-model="user.gender"
                >
                  <v-radio
                    value="1"
                    label=""
                  />
                  <v-radio
                    value="0"
                    label=""
                  />
                </v-radio-group>
                <el-date-picker
                  v-model="user.birthday"
                  type="date"
                  placeholder=""
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
                <div style="height: 5px"></div>
                <v-text-field
                  v-model="user.email"
                  :rules="emailRules"
                  label=""
                  required
                ></v-text-field>

                <v-text-field
                  v-model="user.password"
                  :rules="passwordRules"
                  type="password"
                  label=""
                  required
                ></v-text-field>
                <v-text-field
                  v-model="user.confirmedPassword"
                  type="password"
                  :error-messages="confirmedPasswordRules(user.confirmedPassword)"
                  label=""
                  required
                ></v-text-field>
                <v-row>
                  <v-col>
                    <v-text-field
                      v-model="user.verificationCode"
                      type="text"
                      :error-messages="verificationRules(user.verificationCode)"
                      label=""
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-btn
                      class="ma-2"
                      outlined
                      color="indigo"
                      @click="fun_verification"
                    >
                      
                    </v-btn>
                  </v-col>
                </v-row>
                <div style="height: 20px"></div>
                <el-button
                  type="primary"
                  :disabled="!valid"
                  @click="validate"
                ></el-button>

                <el-button
                  type="info"
                  @click="reset"
                ></el-button>
                <el-button
                  type="info"
                  @click="fun_login"
                ></el-button>

              </v-form>
            </el-card>
          </el-main>
        </el-container>
      </el-col>
    </el-row>
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col
        :span="6"
        style="height:100px"
      >
      </el-col>
    </el-row>
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col
        :span="6"
        style="height:100px"
      >
      </el-col>
    </el-row>
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col
        :span="6"
        style="height:100px"
      >
      </el-col>
    </el-row>
    <el-row
      type="flex"
      justify="center"
    >
      <!-- justify  -->
      <el-col
        :span="6"
        style="height:100px"
      >
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'Register',
    data: () => ({
      user: {
        username: '',
        gender: '',
        birthday: '2022-02-01',
        email: '',
        password: '',
        confirmedPassword: '',
        verificationCode: '',
      },
      valid: true,
      email: '',
      emailRules: [
        v => !!v || '',
        v => /.+@.+\..+/.test(v) || '',
      ],
      password: '',
      passwordRules: [
        v => !!v || '',
      ],
    }),
    methods: {
      validate () {
        this.$refs.form.validate()
        if (this.$refs.form.validate()) {
          this.fun_register()
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      fun_register: function () {
        var _this = this
        this.$http.post('/register/registerUser.do', {
          username: _this.user.username,
          gender: _this.user.gender,
          birthday: _this.user.birthday,
          email: _this.user.email,
          password: _this.user.password,
          verificationCode: _this.user.verificationCode,
        }).then(function (response) {
          if (response.data.flag) {
            _this.$message({
              message: response.data.errorMsg,
              type: 'success',
            })
            _this.$router.push({ path: '/components/login' }).catch(err => { err = null })
          } else {
            _this.$message({
              message: response.data.errorMsg,
              type: 'warning',
            })
          }
        })
      },
      confirmedPasswordRules: function (v) {
        if (v.length === 0 && this.user.password.length !== 0) {
          return ''
        } else if (v !== this.user.password) {
          return ''
        } else {
          return ''
        }
      },
      verificationRules: function (v) {
        if (v.length === 0 && this.user.confirmedPassword.length !== 0) {
          return ''
        }
      },
      fun_login: function () {
        this.$router.push({ path: '/components/login' }).catch(err => { err = null })
      },
      fun_verification: function () {
        var _this = this
        this.$http.post('/register/checkEmailExist.do', {
          email: _this.user.email,
        }).then(function (response) {
          if (response.data.flag === true) {
            _this.$message({
              message: '5',
              type: 'success',
            })
          } else {
            _this.$message({
              message: '',
              type: 'warning',
            })
          }
        }).catch(function (e) {
          console.log(e)
        })
      },
    },
  }
</script>

